:root {
  --h1-font-size: 3rem;
  --h2-font-size: 2rem;
  --h3-font-size: 1.5rem;
  --p-font-size: 1.25rem;
  --h1-font-weight: 400;
  --h2-font-weight: 400;
  --h3-font-weight: 400;
  --headings-font-family: 'Playfair Display', sans-serif;
  --descriptions-font-family: Roboto, sans-serif;
}

@media (max-width: 1050px) {
  :root {
    --h1-font-size: 2.5rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --p-font-size: 1rem;
  }
}

body {
  margin: 0;
}
